		margin-top: 110px !important;
		background-color: white;
		width: 100%;
	}

	#sayingInput {
		position: fixed;
		width: 100%;
		margin-top: 150px !important;
		margin-left: -12px;
	}

	#sayingList {
		margin-top: 185px;
	}
}
